<template>
  <div>
    <van-nav-bar class="nav" title="公告详情" left-arrow @click-left="goback" />
    <p class="headline">{{ context.title }}</p>
    <span class="time">发布时间：{{ context.time }}</span>
    <van-divider dashed />
    <div class="context">
      <p v-html="context.content">{{ context.content }}</p>
      <p class="publisher">作者：{{ context.publisher }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'New',
  props: ['id'],
  data() {
    return {
      context: {
        title: '',
        publisher: '',
        time: '',
        content: '',
        click: '',
        fileURL: ''
      },
      last: {},
      next: {}
    }
  },
  methods: {
    goback() {
      this.$router.push('/noticeList')
    },
    async getNew() {
      let res = await this.$http.get('/api/home/announce?id=' + this.id)
      if (res.status !== 200) return false
      if (res.data.code === 200) {
        this.context = res.data.data.announce
        this.filename = res.data.data.filename
        if (this.news.context.length !== 0) {
          this.news.context = this.news.context.replace(
            /img/g,
            'img style = " max-width:calc(100% - 1rem/2); "'
          )
        }
      }
    }
  },
  mounted() {
    this.getNew()
  }
}
</script>

<style scoped>
.nav {
  background-color: #333744;
}
.headline {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  margin-top: 0.5rem;
}
.publisher {
  margin-left: 0.5rem;
  margin-top: 0.5rem;
  color: #808080;
}
.time {
  color: #808080;
  margin-top: 0.75rem;
  font-size: 0.8125rem;
  margin-left: 0.5rem;
}
.context {
  margin: 0 0.5rem;
  min-height: 23.125rem;
  line-height: 150%;
}
.lastNext {
  margin-left: 0.5rem;
}
</style>
